.wrapper-popper {
  width: 100%;
  height: 100%;
  max-height: min((100vh - 96px) - 60px, 734px);
  overflow-y: auto;
  background: rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.12) 0px 2px 12px;
  padding-top: 8px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}
